* {
  margin: 0;
}
header {
  background-color: black;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  max-height: 40vh;
  height: 100px;
  position: sticky;
  top: 0;
  z-index: 1;
  a {
    width: 20%;
  }
  .logo {
    width: 7rem;
    padding: 10px;
    padding-left: 30px;
  }
  .search {
    width: 40%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
  .searchBar {
    color: black;
    width: 200px;
    height: 30px;
    border-radius: 15px;
    background-color: white;
  }
  .botonesAudio {
    display: flex;
    padding-right: 10rem;
    img {
      width: 2.8rem;
    }
    .botonTransparente {
      border: none;
      cursor: pointer;
      appearance: none;
      background-color: inherit;
      padding-left: 2rem;
    }
  }

  .logo-busq {
    width: 40px;
    height: 45px;
    padding: 10px;
  }
  nav {
    font-family: "Inter", sans-serif;
    display: flex;
    padding: 10px;
    width: 45%;
    align-items: flex-end;
    justify-content: flex-end;
    flex-direction: row;
  }

  // MENU COPIADO DE INTERNET
  .botonMenu {
    margin: 0 auto;

    display: block;

    width: 40px;

    height: 36px;

    padding: 10px;

    border-radius: 7px;

    //background: #666;

    color: white;

    cursor: pointer;

    position: relative;
  }

  .botonMenu {
    .imgMenu {
      display: block;

      width: 40px;

      /*height: 4px;
  
        background: white;
  
        margin-top: 7px;*/
    }
  }

  label {
    cursor: pointer;
  }

  #menu-toggle {
    display: none;
  }

  #trickMenu {
    display: none;
    border-radius: 0px 0px 20px 20px;
    text-align: center;

    background: white;

    padding: 10px 0;
    position: absolute;
    right: 0;
    z-index: 1;
    top: 100px;
    width: 300px;
    list-style: none;
  }

  #trickMenu li a {
    display: block;
    text-align: center;
    padding: 10px 0;
    font-size: 1.7rem;
    width: auto;
    color: black;
    text-decoration: none;
  }
  #trickMenu li a:hover {
    transition: 0.7s;
    width: auto;
    background-color: black;
    color: white;
  }

  #menu-toggle:checked + #trickMenu {
    text-align: center;
    display: block;
  }
}

@media (min-width: 621px) and (max-width: 745px) {
  header {
    .botonMenu {
      padding-right: 25px;
      padding-bottom: 7px;
    }
    .botonesAudio {
      img {
        width: 2rem;
      }
    }
  }

  .search {
    visibility: hidden;
  }
}

@media (max-width: 620px) {
  header {
    .botonMenu {
      padding-right: 30px;
      padding-bottom: 7px;
    }
    .botonesAudio {
      padding-right: 1rem;
      img {
        width: 2rem;
      }
    }
    .logo {
      width: 120px;
    }
    .search {
      visibility: hidden;
    }
    #trickMenu {
      display: none;

      text-align: center;

      padding: 10px 0;
      position: absolute;
      right: 0;
      z-index: 1;
      top: 100px;
      width: 130px;
      list-style: none;
    }
  }
}
